import React, { Component } from "react";
import { S, wait } from "./alabsi";
import { userData, data } from "./Data";
import { dateGetter, dateIndex } from "./MainPage";
import DailyDetails from "./DailyDetails";

let arithmetic;
export default class Summary extends Component {
	componentDidMount() {
		this.introPageFadeTransition();
		window.scrollTo({ top: 0 });
		this.chartLastWeekKcal();
		this.chartLastWeekNutritions();
	}

	introPageFadeTransition = async _ => {
		S("#summaryPage").css("opacity", 0).css("transition", "opacity 350ms");
		await wait(20);
		S("#summaryPage").css("opacity", 1);
		await wait(350);
		S("#summaryPage").removeAttr("style");
	};

	NutritionSum = (n, d) => {
		const getDataByDate = userData.diary.filter(e => e.date === dateGetter(d))[0];
		let sum = 0;
		if (getDataByDate) {
			for (let i in getDataByDate) {
				if (i !== "date") {
					const getDataById = getDataByDate[i].map(
						e => (e = Object.assign({}, data.filter(el => el.id === e[0])[0], { value: e[1], input: e[2] }))
					);

					sum += getDataById.map(e => e[n] * ((e.value * e.input) / 100)).reduce((a, b) => a + b);
				}
			}
		}
		return sum;
	};

	chartLastWeekKcal = async () => {
		await wait(200);
		for (let i = 0; i > -8; i--) {
			const height = Math.round((this.NutritionSum("kcal", i) / userData.userInfo.dailykcal) * 100);
			S("#KcalChart" + i * -1).css("height", height + "%");
			S("#KcalChart" + i * -1 + " p").text(height + "%");
		}
	};

	chartLastWeekNutritions = async () => {
		await wait(200);
		for (let i = 0; i > -8; i--) {
			const heightCarb = Math.min(Math.max(Math.round(((this.NutritionSum("carb", i) * 4) / userData.userInfo.dailykcal) * 100), 0), 100);
			const heightProtein = Math.min(Math.max(Math.round(((this.NutritionSum("protein", i) * 4) / userData.userInfo.dailykcal) * 100), 0), 100);
			const heightFat = Math.min(Math.max(Math.round(((this.NutritionSum("fat", i) * 9) / userData.userInfo.dailykcal) * 100), 0), 100);
			S("#NutritionschartsCarb" + i * -1).css("height", heightCarb + "%");
			S("#NutritionschartsProtein" + i * -1)
				.css("height", heightProtein + "%");
				// .css("max-height", Math.abs(100 - (heightCarb + heightFat)) + "%");
			S("#NutritionschartsFat" + i * -1)
				.css("height", heightFat + "%")
				.css("max-height", Math.abs(100 - (heightCarb + heightProtein)) + 15 + "%");
		}
	};

	lastWeekKcalArithmetic = () => {
		let ArithmeticArray = [];
		for (let i = 0; i > -8; i--) {
			if (this.NutritionSum("kcal", i) !==0) ArithmeticArray.push(this.NutritionSum("kcal", i));
		}
		if (ArithmeticArray.length > 0) {
			arithmetic =  ArithmeticArray.length;
			return Math.round(ArithmeticArray.reduce((a, b) => a + b) / ArithmeticArray.length);
		} else return 0;
	};

	NutritionSumByMeal = (n, d, m) => {
		const getDataByDate = userData.diary.filter(e => e.date === dateGetter(d))[0];
		if (getDataByDate && getDataByDate[m]) {
			const getDataById = getDataByDate[m].map(e => (e = Object.assign({}, data.filter(el => el.id === e[0])[0], { value: e[1], input: e[2] })));
			return getDataById.map(e => e[n] * ((e.value * e.input) / 100)).reduce((a, b) => a + b);
		} else return 0;
	};

	chartLastWeekPercentByMeal = m => {
		let ArithmeticArray = [];
		for (let i = 0; i > -8; i--) {
			if (this.NutritionSumByMeal("kcal", i, m) !== 0) ArithmeticArray.push(this.NutritionSumByMeal("kcal", i, m));
		}
		if (ArithmeticArray.length > 0) {
			return Math.round((ArithmeticArray.reduce((a, b) => a + b) / arithmetic / this.lastWeekKcalArithmetic()) * 100);
		} else return 0;
	};

	NutritionSumByFood = (d, f) => {
		const getDataByDate = userData.diary.filter(e => e.date === dateGetter(d))[0];
		let sum = 0;
		if (getDataByDate) {
			for (let i in getDataByDate) {
				if (i !== "date") {
					const getDataById = getDataByDate[i].map(
						e => (e = Object.assign({}, data.filter(el => el.id === e[0])[0], { value: e[1], input: e[2] }))
					);
					sum += getDataById.map(e => (e.description === f ? e.kcal * ((e.value * e.input) / 100) : 0)).reduce((a, b) => a + b);
				}
			}
		}
		return sum;
	};

	chartLastWeekPercentByFood = f => {
		let ArithmeticArray = [];
		for (let i = 0; i > -8; i--) {
			if (this.NutritionSumByFood(i, f) !== 0) ArithmeticArray.push(this.NutritionSumByFood(i, f));
		}
		if (ArithmeticArray.length > 0) {
			return Math.round((ArithmeticArray.reduce((a, b) => a + b) / arithmetic / this.lastWeekKcalArithmetic()) * 100);
		} else return 0;
	};

	render() {
		return (
			<div id='summaryPage'>
				<header id='summaryPageHeader'>
					<p id='summaryPageHeaderTitle'>الــمــوجــز</p>
				</header>
				<div id='summaryLastWeekKcalContainer'>
					<p id='summaryLastWeekKcalTitle'>السعرات المستهلكة لأسبوع</p>
					<div id='summaryLastWeekKcalNumContainer'>
						<p>{userData.userInfo.dailykcal}</p>
						<p>{Math.round(userData.userInfo.dailykcal / 2)}</p>
						<p>0</p>
					</div>
					<div id='summaryLastWeekKcalChartContainer'>
						<div id='chartMiddleLine'></div>
						<div className='Kcalcharts' id='KcalChart0'>
							<p>0%</p>
						</div>
						<div className='Kcalcharts' id='KcalChart1'>
							<p>0%</p>
						</div>
						<div className='Kcalcharts' id='KcalChart2'>
							<p>0%</p>
						</div>
						<div className='Kcalcharts' id='KcalChart3'>
							<p>0%</p>
						</div>
						<div className='Kcalcharts' id='KcalChart4'>
							<p>0%</p>
						</div>
						<div className='Kcalcharts' id='KcalChart5'>
							<p>0%</p>
						</div>
						<div className='Kcalcharts' id='KcalChart6'>
							<p>0%</p>
						</div>
					</div>
				</div>
				<div id='summaryLastWeekNutritionsContainer'>
					<p id='summaryLastWeekNutritionsTitle'>متوسط السعرات حسب المغذيات لأسبوع</p>
					<div id='summaryLastWeekNutritionsNumContainer'>
						<p>{userData.userInfo.dailykcal}</p>
						<p>{Math.round(userData.userInfo.dailykcal / 2)}</p>
						<p>0</p>
					</div>
					<div id='summaryLastWeekNutritionsChartContainer'>
						<div id='chartMiddleLine'></div>
						<div className='Nutritionscharts'>
							<div id='NutritionschartsCarb0'></div>
							<div id='NutritionschartsProtein0'></div>
							<div id='NutritionschartsFat0'></div>
						</div>
						<div className='Nutritionscharts'>
							<div id='NutritionschartsCarb1'></div>
							<div id='NutritionschartsProtein1'></div>
							<div id='NutritionschartsFat1'></div>
						</div>
						<div className='Nutritionscharts'>
							<div id='NutritionschartsCarb2'></div>
							<div id='NutritionschartsProtein2'></div>
							<div id='NutritionschartsFat2'></div>
						</div>
						<div className='Nutritionscharts'>
							<div id='NutritionschartsCarb3'></div>
							<div id='NutritionschartsProtein3'></div>
							<div id='NutritionschartsFat3'></div>
						</div>
						<div className='Nutritionscharts'>
							<div id='NutritionschartsCarb4'></div>
							<div id='NutritionschartsProtein4'></div>
							<div id='NutritionschartsFat4'></div>
						</div>
						<div className='Nutritionscharts'>
							<div id='NutritionschartsCarb5'></div>
							<div id='NutritionschartsProtein5'></div>
							<div id='NutritionschartsFat5'></div>
						</div>
						<div className='Nutritionscharts'>
							<div id='NutritionschartsCarb6'></div>
							<div id='NutritionschartsProtein6'></div>
							<div id='NutritionschartsFat6'></div>
						</div>
					</div>
					<div id='summaryLastWeekNutritionsListContainer'>
						<p>الكربوهيدرات</p>
						<p>البروتين</p>
						<p>الدهون</p>
					</div>
				</div>
				<div id='summaryLastWeekKcalArithmeticContainer'>
					<p id='summaryLastWeekKcalArithmeticTitle'>متوسط السعرات المستهلكة لأسبوع</p>
					<p id='summaryLastWeekKcalArithmetic'>
						{this.lastWeekKcalArithmetic()}
						<span>سعرة حرارية</span>
					</p>
				</div>
				<div id='summaryLastWeekKcalByMealContainer'>
					<p id='summaryLastWeekKcalByMealTitle'>متوسط السعرات حسب الوجبة لأسبوع</p>
					<div id='summaryByMealPercentContainer'>
						<div style={{ backgroundColor: "#424242", width: this.chartLastWeekPercentByMeal("breakfast") + "%" }}></div>
						<div style={{ backgroundColor: "#82827c", width: this.chartLastWeekPercentByMeal("lunch") + "%" }}></div>
						<div style={{ backgroundColor: "#B5B5AC", width: this.chartLastWeekPercentByMeal("dinner") + "%" }}></div>
						<div style={{ backgroundColor: "#E3E3D8", width: this.chartLastWeekPercentByMeal("snacks") + "%" }}></div>
					</div>
					<div id='summaryByMealDetailsContainer'>
						<div id='summaryByMealDetailsTitlesContainer'>
							<p id='summaryByMealDetailsBreakfast'>الفطور</p>
							<p id='summaryByMealDetailsLunch'>الغداء</p>
							<p id='summaryByMealDetailsDinner'>العشاء</p>
							<p id='summaryByMealDetailsSnacks'>وجبة خفيفة</p>
						</div>
						<div id='summaryByMealDetailsPercentContainer'>
							<p>
								{this.chartLastWeekPercentByMeal("breakfast")}
								<span>%</span>
							</p>
							<p>
								{this.chartLastWeekPercentByMeal("lunch")}
								<span>%</span>
							</p>
							<p>
								{this.chartLastWeekPercentByMeal("dinner")}
								<span>%</span>
							</p>
							<p>
								{this.chartLastWeekPercentByMeal("snacks")}
								<span>%</span>
							</p>
						</div>
					</div>
				</div>
				<div id='summaryLastWeekKcalByFoodContainer'>
					<p id='summaryLastWeekKcalByFoodTitle'>متوسط السعرات حسب النوع لأسبوع</p>
					<div id='summaryByFoodDetailsContainer'>
						<div id='summaryByFoodDetailsTitlesContainer'>
							<p>الأسماك</p>
							<p>البيض ومنتجات الحليب</p>
							<p>البقوليات</p>
							<p>الحبوب</p>
							<p>الخضروات</p>
							<p>الدبس</p>
							<p>الدهون</p>
							<p>الفروج</p>
							<p>الفواكه</p>
							<p>المعجنات</p>
							<p>المكسرات</p>
							<p>المكملات غذائية</p>
						</div>
						<div id='summaryByFoodDetailsPercentContainer'>
							<p>
								{this.chartLastWeekPercentByFood("أسماك")}
								<span>%</span>
							</p>
							<p>
								{this.chartLastWeekPercentByFood("البيض ومنتجات الحليب")}
								<span>%</span>
							</p>
							<p>
								{this.chartLastWeekPercentByFood("بقوليات")}
								<span>%</span>
							</p>
							<p>
								{this.chartLastWeekPercentByFood("حبوب")}
								<span>%</span>
							</p>
							<p>
								{this.chartLastWeekPercentByFood("خضار")}
								<span>%</span>
							</p>
							<p>
								{this.chartLastWeekPercentByFood("دبس")}
								<span>%</span>
							</p>
							<p>
								{this.chartLastWeekPercentByFood("دهون")}
								<span>%</span>
							</p>
							<p>
								{this.chartLastWeekPercentByFood("فروج")}
								<span>%</span>
							</p>
							<p>
								{this.chartLastWeekPercentByFood("فاكهة")}
								<span>%</span>
							</p>
							<p>
								{this.chartLastWeekPercentByFood("معجنات")}
								<span>%</span>
							</p>
							<p>
								{this.chartLastWeekPercentByFood("مكسرات")}
								<span>%</span>
							</p>
							<p>
								{this.chartLastWeekPercentByFood("مكملات غذائية")}
								<span>%</span>
							</p>
						</div>
					</div>
				</div>
				<div id='dailyDetailsContainer'>
					<DailyDetails key={Math.random() * 1000} date={dateGetter(dateIndex)} />
				</div>
			</div>
		);
	}
}
