import React, { Component } from "react";
import { S, wait } from "./alabsi";
import { addedByUsers, data, userData } from "./Data";
import { updateRenderState } from ".";
import FavoritePage from "./FavoritePage";
import NavicationBar from "./NavicationBar";

let tmpFood;

export default class CreateFood extends Component {
	componentDidMount() {
		this.introPageTransition();
		tmpFood = {
			id: "addedByUsers" + addedByUsers.length,
			name: "",
			description: "آخر",
			kcal: 0,
			fat: 0,
			saturatedFat: 0,
			unsaturatedFat: 0,
			carb: 0,
			fiber: 0,
			sugar: 0,
			protein: 0,
			cholesterol: 0,
			sodium: 0,
			potassium: 0,
		};
	}

	doneHandle = async () => {
		addedByUsers.push(tmpFood);
		data.push(tmpFood);
		window.localStorage.setItem("addedByUsers", JSON.stringify(addedByUsers));
		userData.favorite.food.unshift(tmpFood.id);
		window.history.go(-2);
        window.history.pushState("", "", "/favorites");
        await wait(50)
		updateRenderState([
			<FavoritePage key={Math.random() * 100} hashName={window.location.hash.split("#")[1]} />,
			<NavicationBar key={Math.random() * 100} />,
		]);
		console.log(addedByUsers);
	};

	introPageTransition = async () => {
		S("#createFoodPage")
			.css("position", "fixed")
			.css("bottom", "-100vh")
			.css("left", "50%")
			.css("opacity", "0")
			.css("transform", "translate(-50%, 100%)");
		S("#createFoodHeader").css("position", "relative");
		await wait(10);
		S("#createFoodPage").css("bottom", "100%").css("opacity", "1");
		await wait(S("#createFoodPage").getStyle("transition-duration", true) * 1000);
		S("#createFoodPage").removeAttr("style");
		S("#createFoodHeader").removeAttr("style");
	};

	backButtonHandle = () => window.history.back();

	render() {
		return (
			<div id='createFoodPage'>
				<header id='createFoodHeader'>
					<svg id='createFoodBack' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' onClick={this.backButtonHandle}>
						<path d='M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z' />
					</svg>
					<p id='createFoodHeaderTitle' onClick={this.doneHandle}>
						حــفــظ
					</p>
				</header>
				<div className='createFoodItemsContainer'>
					<p className='createFoodTitles'>اسـم الطـعـام</p>
					<input id='foodName' type='text' placeholder='اسـم الطـعـام' onChange={e => (tmpFood.name = e.target.value)} />
				</div>
				<div className='createFoodItemsContainer'>
					<p className='createFoodTitles'>اخـتـر الصـنـف</p>
					<select id='chooseCategorie' className='createFoodInputs' defaultValue='آخر' onChange={e => (tmpFood.description = e.target.value)}>
						<option value='أسماك'>الأسماك</option>
						<option value='البيض ومنتجات الحليب'>البيض ومنتجات الحليب</option>
						<option value='بقوليات'>البقوليات</option>
						<option value='حبوب'>الحبوب</option>
						<option value='خضار'>الخضروات</option>
						<option value='دبس'>الدبس</option>
						<option value='دهون'>الدهون</option>
						<option value='فروج'>الفروج</option>
						<option value='فاكهة'>الفواكه</option>
						<option value='معجنات'>المعجنات</option>
						<option value='مكسرات'>المكسرات</option>
						<option value='مكملات غذائية'>المكملات غذائية</option>
						<option value='آخر'>آخر</option>
					</select>
				</div>
				<div className='createFoodItemsContainer'>
					<p className='createFoodTitles'>السعرات الحرارية كل 100 غرام</p>
					<input
						id='foodKcal'
						type='number'
						defaultValue={0}
						onChange={e => (tmpFood.kcal = Number(e.target.value))}
						onClick={e => e.target.select()}
					/>
				</div>
				<div id='createFoodNutritionInfoDetail'>
					<div id='createFoodNutritionInfoTitles'>
						<p className='createFoodNutritionInfoMain'>البروتين</p>
						<p className='createFoodNutritionInfoMain'>الكربوهيدرات</p>
						<p className='createFoodNutritionInfoSub'>الألياف</p>
						<p className='createFoodNutritionInfoSub'>السكر</p>
						<p className='createFoodNutritionInfoMain'>الدهون</p>
						<p className='createFoodNutritionInfoSub'>الدهون المشبعة</p>
						<p className='createFoodNutritionInfoSub'>الدهون الغير مشبعة</p>
						<p className='createFoodNutritionInfoMain'>آخر</p>
						<p className='createFoodNutritionInfoSub'>الكوليسترول</p>
						<p className='createFoodNutritionInfoSub'>الصوديوم</p>
						<p className='createFoodNutritionInfoSub'>البوتاسيوم</p>
					</div>
					<div id='createFoodNutritionInfoValues'>
						<p className='createFoodNutritionInfoMain'>
							غ{" "}
							<input
								type='number'
								defaultValue={0}
								onChange={e => (tmpFood.protein = Number(e.target.value))}
								onClick={e => e.target.select()}
							/>
						</p>
						<p className='createFoodNutritionInfoMain'>
							غ{" "}
							<input type='number' defaultValue={0} onChange={e => (tmpFood.carb = Number(e.target.value))} onClick={e => e.target.select()} />
						</p>
						<p className='createFoodNutritionInfoSub'>
							غ{" "}
							<input
								type='number'
								defaultValue={0}
								onChange={e => (tmpFood.fiber = Number(e.target.value))}
								onClick={e => e.target.select()}
							/>
						</p>
						<p className='createFoodNutritionInfoSub'>
							غ{" "}
							<input
								type='number'
								defaultValue={0}
								onChange={e => (tmpFood.sugar = Number(e.target.value))}
								onClick={e => e.target.select()}
							/>
						</p>
						<p className='createFoodNutritionInfoMain'>
							غ{" "}
							<input type='number' defaultValue={0} onChange={e => (tmpFood.fat = Number(e.target.value))} onClick={e => e.target.select()} />
						</p>
						<p className='createFoodNutritionInfoSub'>
							غ{" "}
							<input
								type='number'
								defaultValue={0}
								onChange={e => (tmpFood.saturatedFat = Number(e.target.value))}
								onClick={e => e.target.select()}
							/>
						</p>
						<p className='createFoodNutritionInfoSub'>
							غ{" "}
							<input
								type='number'
								defaultValue={0}
								onChange={e => (tmpFood.unsaturatedFat = Number(e.target.value))}
								onClick={e => e.target.select()}
							/>
						</p>
						<p className='createFoodNutritionInfoMain' style={{ color: "rgb(0, 0, 0, 0)" }}>
							......
						</p>
						<p className='createFoodNutritionInfoSub'>
							غ{" "}
							<input
								type='number'
								defaultValue={0}
								onChange={e => (tmpFood.cholesterol = Number(e.target.value))}
								onClick={e => e.target.select()}
							/>
						</p>
						<p className='createFoodNutritionInfoSub'>
							غ{" "}
							<input
								type='number'
								defaultValue={0}
								onChange={e => (tmpFood.sodium = Number(e.target.value))}
								onClick={e => e.target.select()}
							/>
						</p>
						<p className='createFoodNutritionInfoSub'>
							غ{" "}
							<input
								type='number'
								defaultValue={0}
								onChange={e => (tmpFood.potassium = Number(e.target.value))}
								onClick={e => e.target.select()}
							/>
						</p>
					</div>
				</div>
			</div>
		);
	}
}
